.grid{
    display: grid;
    grid-template-columns: 20% 80%;
}





#header{
        background-color: #96b4f03d;
       
        display: flex;
        justify-content: space-between;
        padding-bottom:10px ;
        padding-top: 10px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
    
    
    }
    .btn{
    
        background-color:#00308F;
        color: white;
       
    display: inline-block;
    border: none;
    border-radius: 5px;
    box-sizing: border-box;
    padding: 9px 20px;
    margin-right: 5px;
    
    }
    
    
    .btn_div{
    
        margin-top: 30px ;
    
    
    }
    
    .mod{
        color: white;
        margin-left: 10px;
        margin-top:5px ;
    }
    
    
    
    .bb{
    display: flex;
    flex-direction: column;
    
    }
    
    
    .input{
    
    
    width:100% ;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    }
    
    
    
    #big{
        display: flex;
        margin-top: 10px;
        margin-bottom: 10px;

       justify-content: space-between;
     
    }
    
    
    

    
    
    #text{
    margin-right: 25px;
    margin-top: 15px;
    
    }
    
    
    table {
        font-family: arial, sans-serif;
        border-collapse: collapse;
        width: 100%;
      }
      
      td, th {
        border: 1px solid #dddddd;
        text-align: left;
        padding: 8px;
      }
      
      
      #next{
    display: flex;
    flex-direction: row;
    justify-content: center;
    
      }
    
    .nex{
        border: 1px solid #ccc;
       
        box-sizing: border-box;
        padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border-radius:4px ;
    }
    
    .pagen{
    background-color: #00308F;
        border: none;
       color: white;
        box-sizing: border-box;
        padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    
    
    
    }
    
    
    #showing{
    margin-top:15px ;
    
    
    
    }
    
    
    

    
    
    